@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  padding: 0;
}
.form{font-size:16px;padding:16px 0}.form p{margin:0 0 16px 16px}.form input{height:32px;width:240px;margin:8px 16px;padding:8px;font-size:1rem;border-radius:10px}.form input.important{margin:0;margin-bottom:16px;width:14px;height:16px}.form label{margin:16px;font-size:1.3rem;font-weight:bold}.form label.important{margin:24px;font-size:1.1rem;text-transform:uppercase}.form button{margin:8px 16px;padding:8px 16px;font-size:1rem;text-transform:uppercase;border-radius:10px;background-color:#fff}.form button:hover{background-color:#fffa8a}

.list{padding:16px}.list h3{font-size:1.5rem;text-transform:uppercase;text-decoration:underline}p{font-size:1.4rem}p button{padding:8px 8px;margin:8px 8px 0 0;border-radius:10px;font-size:0.8rem;text-transform:uppercase;background-color:#fff}p button:hover{background-color:dimgray;color:#fffa8a}.task{display:inline-block;width:-webkit-max-content;width:-moz-max-content;width:max-content;text-align:center;padding:8px;margin:8px;background-color:#fffa8a;color:dimgray}

.App{min-height:100vh;margin:0;padding:16px;font-family:'Roboto Condensed', sans-serif;color:#fff}body{background:url(/to-do/static/media/background.edc8c0c0.jpg) no-repeat center center;background-size:cover}header{width:-webkit-max-content;width:-moz-max-content;width:max-content;margin:16px;text-transform:uppercase;text-align:center;padding:8px 16px;color:dimgray;background-color:#fffa8a}

